:root {
  --accent: 8 48 80;
  --font-general-sans: 'General Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
}

html { 
  scroll-behavior: smooth; 
  scroll-padding-top: 100px; /* Offset for sticky header */
}

/* Focus visible styles for accessibility */
:focus-visible {
  outline: 2px solid rgba(0, 0, 0, 0.3);
  outline-offset: 2px;
}

/* Improve contrast for light text on light backgrounds */
@media (prefers-contrast: high) {
  body {
    --tw-text-opacity: 1;
  }
}

body{
  --tw-text-opacity: 1;
  color: rgb(10 10 10 / var(--tw-text-opacity, 1));
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: transparent;
}

::-moz-selection {
  background: rgba(16, 185, 129, 0.22);
}

::selection {
  background: rgba(16, 185, 129, 0.22);
}

/* Softer rendering */
* {
  text-rendering: geometricPrecision;
}

/* Marquee */
@keyframes marquee {
  0% { transform: translateX(0); }
  100% { transform: translateX(-50%); }
}
.marquee {
  animation: marquee 18s linear infinite;
}
@media (prefers-reduced-motion: reduce) {
  .marquee { animation: none; }
}
@keyframes logo-scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

.animate-logo-scroll {
  animation: logo-scroll 40s linear infinite;
}
/* Southbase logo ticker */
.sb-ticker {
  animation: sb-marquee 26s linear infinite;
  will-change: transform;
}

@keyframes sb-marquee {
  0% { transform: translateX(0); }
  100% { transform: translateX(-33.333%); }
}

@media (prefers-reduced-motion: reduce) {
  .sb-ticker {
    animation: none !important;
    transform: none !important;
  }
}

